.activities__container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  padding: 2rem 1rem;
}

.flip-card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
}

.flip-card {
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 200px;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.flip-card-front {
  background-color: var(--color-bg-varient);
  color: var(--color-primary);
}

.flip-card-back {
  background-color: var(--color-primary);
  color: var(--color-light);
  transform: rotateY(180deg);
}

.flip-card-front h3 {
  margin: 0;
  font-size: 1.1rem;
}

.flip-card-front h4 {
  margin: 0;
  font-size: 0.9rem;
  color: var(--color-secondary);
}

.activity__date {
  font-size: 0.85rem;
  font-style: italic;
  margin-bottom: 0.5rem;
}

.activity__description {
  font-size: 0.9rem;
  text-align: center;
  padding: 0.5rem;
}
